import React, { useState, useEffect } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { Side, Block, TelephoneLineFormData } from '@/types';
import { Loader2, X } from 'lucide-react';

const SIDES: Side[] = ['', ''];
const NORTH_BLOCKS: Block[] = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', '', '', ''];
const SOUTH_BLOCKS: Block[] = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', '', '', ''];

export const DataEntryForm: React.FC = () => {
  const { addLine, updateLine, deleteLine, editingLine, setEditingLine, loading, lines } = useAppStore();
  const [formData, setFormData] = useState<TelephoneLineFormData>({
    side: '',
    block: 'A',
    line_type: '',
    company_name: '',
    phone_number: '',
    core_number: '',
    block_core: '',
    exchange_info: '',
    remarks: '',
  });

  // Dynamic blocks based on side
  const currentBlocks = formData.side === '' ? NORTH_BLOCKS : SOUTH_BLOCKS;

  useEffect(() => {
    if (editingLine) {
      setFormData({
        side: editingLine.side,
        block: editingLine.block,
        line_type: editingLine.line_type || (editingLine.side === '' ? '' : ''),
        company_name: editingLine.company_name,
        phone_number: editingLine.phone_number,
        core_number: editingLine.core_number,
        block_core: editingLine.block_core || '',
        exchange_info: editingLine.exchange_info || '',
        remarks: editingLine.remarks || '',
      });
    } else {
      setFormData({
        side: '',
        block: 'A',
        line_type: '',
        company_name: '',
        phone_number: '',
        core_number: '',
        block_core: '',
        exchange_info: '',
        remarks: '',
      });
    }
  }, [editingLine]);

  // Handle side change to reset block if invalid and set default line_type
  useEffect(() => {
    const blocks = formData.side === '' ? NORTH_BLOCKS : SOUTH_BLOCKS;
    const defaultLineType = formData.side === '' ? '' : '';
    
    setFormData(prev => ({
        ...prev,
        block: blocks.includes(prev.block) ? prev.block : blocks[0],
        line_type: defaultLineType
    }));
  }, [formData.side]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleCancel = () => {
    setEditingLine(null);
  };

  const handleBlockChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const newBlock = e.target.value as Block;
    setFormData(prev => ({
      ...prev,
      block: newBlock,
      block_core: `${newBlock}-`
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validate core number
    if (formData.core_number) {
        const coreNum = parseInt(formData.core_number);
        if (isNaN(coreNum) || coreNum < 1 || coreNum > 100) {
            alert(' 1  100 ');
            return;
        }
    }

    // Check for duplicate core number (Side + Block + Core) - Only check if core_number is provided
    let conflict = undefined;
    if (formData.core_number) {
        conflict = lines.find(l => 
            l.side === formData.side && 
            l.block === formData.block && 
            l.core_number === formData.core_number &&
            l.id !== editingLine?.id
        );
    }

    if (conflict) {
        if (window.confirm('')) {
            try {
                if (editingLine) {
                    // If editing, delete the old record and update the conflicting one
                    const oldId = editingLine.id;
                    await updateLine(conflict.id, formData);
                    await deleteLine(oldId);
                    alert('');
                } else {
                    // If adding, just update the conflicting one
                    await updateLine(conflict.id, formData);
                    alert('');
                    // Reset form to defaults only on successful add
                    setFormData({
                        side: '',
                        block: 'A',
                        line_type: '',
                        company_name: '',
                        phone_number: '',
                        core_number: '',
                        block_core: '',
                        exchange_info: '',
                        remarks: '',
                    });
                }
            } catch (error: any) {
                console.error(error);
                alert(`${error.message || ''}`);
            }
        }
        return;
    }

    try {
      if (editingLine) {
        await updateLine(editingLine.id, formData);
        alert('');
      } else {
        await addLine(formData);
        alert('');
        // Reset form to defaults only on successful add
        setFormData({
            side: '',
            block: 'A',
            line_type: '',
            company_name: '',
            phone_number: '',
            core_number: '',
            block_core: '',
            exchange_info: '',
            remarks: '',
        });
      }
    } catch (error: any) {
      console.error(error);
      alert(`${editingLine ? '' : ''}${error.message || ''}`);
    }
  };

  const inputClass = "w-full border border-slate-700 bg-slate-800 text-slate-200 rounded-md p-1.5 text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none placeholder:text-slate-500 transition-colors";
  const labelClass = "block text-xs font-medium text-slate-300 mb-1";

  return (
    <div className="bg-slate-900 p-4 rounded-lg shadow-lg border border-slate-800">
      <div className="flex items-center justify-between mb-3 border-b border-slate-800 pb-2">
        <h3 className="text-base font-bold text-slate-200">{editingLine ? '' : ''}</h3>
        {editingLine && (
          <button
            onClick={handleCancel}
            className="text-slate-400 hover:text-slate-200 flex items-center gap-1 text-xs transition-colors active:scale-95"
          >
            <X className="w-3.5 h-3.5" />
            
          </button>
        )}
      </div>
      <form onSubmit={handleSubmit} className="space-y-3">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
          <div>
            <label className={labelClass}></label>
            <select
              name="side"
              value={formData.side}
              onChange={handleChange}
              className={inputClass}
            >
              {SIDES.map((s) => (
                <option key={s} value={s}>{s}</option>
              ))}
            </select>
          </div>
          <div>
            <label className={labelClass}></label>
            <select
              name="block"
              value={formData.block}
              onChange={handleBlockChange}
              className={inputClass}
            >
              {currentBlocks.map((b) => (
                <option key={b} value={b}>{b}</option>
              ))}
            </select>
          </div>
          <div className="md:col-span-2">
            <label className={labelClass}></label>
            <div className="flex gap-4 mt-1">
                <label className="flex items-center cursor-pointer">
                    <input 
                        type="radio" 
                        name="line_type" 
                        value="" 
                        checked={formData.line_type === ''}
                        onChange={handleChange}
                        className="w-4 h-4 text-blue-500 border-slate-700 bg-slate-800 focus:ring-blue-500 focus:ring-offset-slate-900"
                    />
                    <span className="ml-2 text-slate-300 text-sm"></span>
                </label>
                <label className="flex items-center cursor-pointer">
                    <input 
                        type="radio" 
                        name="line_type" 
                        value="" 
                        checked={formData.line_type === ''}
                        onChange={handleChange}
                        className="w-4 h-4 text-green-500 border-slate-700 bg-slate-800 focus:ring-green-500 focus:ring-offset-slate-900"
                    />
                    <span className="ml-2 text-slate-300 text-sm"></span>
                </label>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
          <div>
            <label className={labelClass}> (1-100)</label>
            <input
              type="number"
              min="1"
              max="100"
              name="core_number"
              value={formData.core_number}
              onChange={handleChange}
              className={inputClass}
              placeholder=" (: 50)"
            />
          </div>
          <div>
            <label className={labelClass}> ()</label>
            <input
              type="text"
              name="block_core"
              value={formData.block_core || ''}
              onChange={handleChange}
              className={inputClass}
              placeholder=": A-50"
            />
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
          <div>
            <label className={labelClass}> <span className="text-red-500">*</span></label>
            <input
              type="text"
              name="company_name"
              value={formData.company_name}
              onChange={handleChange}
              required
              className={inputClass}
              placeholder=""
            />
          </div>
          <div>
            <label className={labelClass}> <span className="text-red-500">*</span></label>
            <input
              type="tel"
              name="phone_number"
              value={formData.phone_number}
              onChange={handleChange}
              required
              className={inputClass}
              placeholder=""
            />
          </div>
        </div>

        <div>
           <label className={labelClass}></label>
           <input
             type="text"
             name="exchange_info"
             value={formData.exchange_info}
             onChange={handleChange}
             className={inputClass}
             placeholder=""
           />
        </div>

        <div>
          <label className={labelClass}></label>
          <textarea
            name="remarks"
            value={formData.remarks}
            onChange={handleChange}
            rows={3}
            className={inputClass}
            placeholder="..."
          />
        </div>

        <button
          type="submit"
          disabled={loading}
          className={`w-full text-white py-1.5 px-4 rounded-md transition-all flex items-center justify-center disabled:opacity-50 disabled:cursor-not-allowed active:scale-95 shadow-md text-sm ${
            editingLine 
              ? 'bg-blue-600 hover:bg-blue-700 shadow-blue-900/20' 
              : 'bg-slate-700 hover:bg-slate-600 shadow-slate-900/20'
          }`}
        >
          {loading ? (
            <>
              <Loader2 className="w-3.5 h-3.5 mr-2 animate-spin" />
              {editingLine ? '...' : '...'}
            </>
          ) : (
            editingLine ? '' : ''
          )}
        </button>
      </form>
    </div>
  );
};
