import React, { useMemo, useState, useRef, useCallback } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { cn } from '@/lib/utils';
import { Side, Block } from '@/types';
import { ChevronDown, ChevronUp, X } from 'lucide-react';
import { LoginModal } from './LoginModal';

// Create an array [1, ..., 100]
const GRID_CELLS = Array.from({ length: 100 }, (_, i) => i + 1);

const NORTH_LAYOUT: (Block | null)[][] = [
  ['', 'G', 'D', 'A'],
  ['', 'H', 'E', 'B'],
  ['', 'I', 'F', 'C'],
];

const SOUTH_LAYOUT: (Block | null)[][] = [
  ['J', null, null, null],
  ['', 'G', 'D', 'A'],
  ['', 'H', 'E', 'B'],
  ['', 'I', 'F', 'C'],
];

interface BlockViewProps {
  side: Side;
  layout: (Block | null)[][];
  activeBlocks: Set<string>;
  filteredLines: any[];
  hasSearched: boolean;
  onBlockClick: (block: Block, lines: any[], side: Side) => void;
  ignoreSideFilter?: boolean;
}

const useLongPress = (callback: () => void, ms = 500) => {
  const [startLongPress, setStartLongPress] = useState(false);
  const timerRef = useRef<NodeJS.Timeout>();

  const start = useCallback(() => {
    setStartLongPress(true);
    timerRef.current = setTimeout(() => {
      callback();
    }, ms);
  }, [callback, ms]);

  const stop = useCallback(() => {
    setStartLongPress(false);
    if (timerRef.current) {
      clearTimeout(timerRef.current);
    }
  }, []);

  return {
    onMouseDown: start,
    onMouseUp: stop,
    onMouseLeave: stop,
    onTouchStart: start,
    onTouchEnd: stop,
  };
};

const BlockDetailList: React.FC<{ detail: { block: Block; lines: any[]; side: Side }; onClose: () => void }> = ({ detail, onClose }) => {
  const { isAuthenticated, setEditingLine, setActiveTab, setSelectedCore } = useAppStore();
  const [isLoginOpen, setIsLoginOpen] = useState(false);
  const [pendingLine, setPendingLine] = useState<any>(null);

  const handleEdit = (line: any) => {
    setEditingLine(line);
    setActiveTab('manage');
    // Clear any selection state that might interfere or just to be clean
    setSelectedCore(null, null, null, null);
  };

  const handleLongPress = (line: any) => {
    if (isAuthenticated) {
      handleEdit(line);
    } else {
      setPendingLine(line);
      setIsLoginOpen(true);
    }
  };

  const handleLoginSuccess = () => {
    if (pendingLine) {
      handleEdit(pendingLine);
      setPendingLine(null);
    }
  };

  return (
    <>
      <div className="bg-slate-900 rounded-lg border border-slate-800 p-3 mt-4 animate-in slide-in-from-top-2 duration-200 w-full max-w-[320px] mx-auto md:mx-0">
        <div className="flex justify-between items-center mb-2 border-b border-slate-800 pb-2">
           <h3 className="text-sm font-bold text-slate-100">
              {detail.side} -  {detail.block}
           </h3>
           <button onClick={onClose} className="text-slate-400 hover:text-slate-200"><X className="w-4 h-4" /></button>
        </div>
        <div className="max-h-[300px] overflow-y-auto space-y-2 pr-1 scrollbar-thin scrollbar-thumb-slate-700">
           {detail.lines.map((line: any, idx: number) => (
              <BlockDetailItem key={idx} line={line} onLongPress={() => handleLongPress(line)} />
           ))}
        </div>
      </div>
      <LoginModal 
        isOpen={isLoginOpen} 
        onClose={() => {
          setIsLoginOpen(false);
          setPendingLine(null);
        }}
        onSuccess={handleLoginSuccess}
      />
    </>
  );
};

const BlockDetailItem: React.FC<{ line: any; onLongPress: () => void }> = ({ line, onLongPress }) => {
  const longPressProps = useLongPress(onLongPress, 800); // 800ms long press

  return (
    <div 
      {...longPressProps}
      className="bg-slate-800/50 p-2 rounded border border-slate-700/50 hover:bg-slate-800 transition-colors select-none cursor-pointer active:scale-95 active:border-blue-500/50"
    >
       <div className="flex justify-between items-center mb-1">
          <span className="text-blue-400 font-mono font-bold text-sm">
            {line.block_core || (line.core_number ? `${line.block}-${line.core_number}` : line.block)}
          </span>
          <span className={cn("text-xs px-1.5 py-0.5 rounded", line.side === '' ? "bg-blue-900/50 text-blue-300" : "bg-green-900/50 text-green-300")}>
            {line.side}
          </span>
       </div>
       <div className="flex justify-between items-center text-xs text-slate-500 mb-1">
            <span>{line.core_number ? `${line.core_number} ` : ''}</span>
       </div>
       <div className="text-sm text-slate-300 truncate">{line.company_name}</div>
       <div className="text-xs text-slate-400 truncate">{line.phone_number}</div>
    </div>
  );
};

const BlockView: React.FC<BlockViewProps> = ({ side, layout, activeBlocks, filteredLines, hasSearched, onBlockClick, ignoreSideFilter = false }) => {
  return (
    <div className="flex flex-col items-center">
      <div className="flex flex-col gap-1">
        {layout.map((row, rowIndex) => (
          <div key={rowIndex} className="flex gap-1">
            {row.map((block, colIndex) => {
              if (!block) return <div key={colIndex} className="w-7 h-7 md:w-9 md:h-9" />; // Spacer
              
              // If ignoreSideFilter is true, activeBlocks contains just block names ("A").
              // Otherwise it contains "Side-Block" ("-A").
              const isActive = ignoreSideFilter 
                ? activeBlocks.has(block)
                : activeBlocks.has(`${side}-${block}`);
              
              if (!isActive) {
                // Render light gray block for non-active blocks
                return (
                  <div
                    key={block}
                    className="w-7 h-7 md:w-9 md:h-9 border-2 border-slate-800 bg-slate-900/30 text-slate-700 flex items-center justify-center text-[9px] md:text-xs font-bold rounded-lg select-none"
                  >
                    {block}
                  </div>
                );
              }

              // Filter lines for this block
              // If ignoreSideFilter is true, we want all lines in this block regardless of side
              const linesInBlock = filteredLines.filter(l => 
                (ignoreSideFilter ? true : l.side?.trim() === side) && 
                l.block?.trim() === block
              );
              
              let displayText: string = block;
              
              if (hasSearched && linesInBlock.length > 0) {
                  // If we have lines, try to find a block_core value
                  // Priority 1: Display block_core if available
                  const lineWithBlockCore = linesInBlock.find(l => l.block_core);
                  if (lineWithBlockCore && lineWithBlockCore.block_core) {
                      displayText = lineWithBlockCore.block_core;
                  } else {
                      // Priority 2: Display Block-Core format (e.g. "A-100")
                      displayText = `${block}-${linesInBlock[0].core_number}`;
                  }
              }

              return (
                <div
                  key={block}
                  onClick={() => onBlockClick(block, linesInBlock, side)}
                  className={cn(
                    "w-7 h-7 md:w-9 md:h-9 border-2 flex items-center justify-center text-[9px] md:text-xs font-bold rounded-lg transition-all duration-200 select-none break-all text-center leading-tight p-0.5 cursor-pointer active:scale-95",
                    isActive
                      ? "border-blue-500 bg-blue-900/30 text-blue-400 shadow-[0_0_15px_rgba(59,130,246,0.5)] hover:bg-blue-800/50"
                      : "border-slate-700 bg-slate-800 text-slate-500"
                  )}
                >
                  {displayText}
                  {isActive && (
                    <div className="absolute w-1.5 h-1.5 bg-red-500 rounded-full top-1 right-1 animate-pulse shadow-[0_0_8px_rgba(239,68,68,0.8)]" />
                  )}
                </div>
              );
            })}
          </div>
        ))}
      </div>
    </div>
  );
};

interface CoreGridProps {
  title: string;
  colorClass: string;
  data: any[];
  selectedCore: { side?: string; block?: string; core: string; line_type?: string } | null;
  lineType: string;
  onSelect: (lineType: string, core: string) => void;
}

const CoreGrid: React.FC<CoreGridProps> = ({ title, colorClass, data, selectedCore, lineType, onSelect }) => {
  const hasData = (cellNumber: number) => {
    return data.some((line) => line.core_number === String(cellNumber));
  };

  const isSelected = (cellNumber: number) => {
    return (
      selectedCore?.line_type === lineType &&
      selectedCore?.core === String(cellNumber)
    );
  };

  return (
    <div className="flex flex-col items-center bg-slate-900 p-2 rounded-lg shadow-md border border-slate-800 w-full overflow-hidden">
      <h4 className="text-sm font-bold mb-2 flex items-center gap-2 text-slate-200">
        <span className={cn("w-1.5 h-1.5 rounded-full shadow-[0_0_8px_currentColor]", colorClass)} />
        {title}
      </h4>
      <div className="overflow-x-auto w-full pb-1 scrollbar-thin scrollbar-thumb-slate-700 scrollbar-track-transparent">
        <div className="grid grid-cols-10 gap-1 min-w-fit mx-auto p-0.5">
        {GRID_CELLS.map((num) => {
          const has = hasData(num);
          const selected = isSelected(num);
          return (
            <div
              key={num}
              onClick={() => {
                if (selected) {
                  onSelect('', ''); 
                } else if (has) {
                  onSelect(lineType, String(num));
                }
              }}
              className={cn(
                "relative w-7 h-7 md:w-8 md:h-8 border flex items-center justify-center cursor-pointer transition-all duration-100 text-[10px] md:text-xs select-none rounded active:scale-90",
                selected
                  ? "border-blue-400 bg-blue-600 text-white z-10 ring-1 ring-blue-400 shadow-[0_0_10px_rgba(59,130,246,0.6)]"
                  : "border-slate-700 bg-slate-800 hover:bg-slate-700",
                !has && !selected && "opacity-30 bg-slate-900 border-slate-800 text-slate-600 cursor-default",
                has && !selected && "text-slate-200 hover:border-slate-500"
              )}
            >
              <span className="font-medium">{num}</span>
              {has && !selected && (
                <div className="absolute top-0 right-0 w-1.5 h-1.5 bg-red-500 rounded-full -mr-0.5 -mt-0.5 shadow-[0_0_5px_rgba(239,68,68,0.8)]" />
              )}
            </div>
          );
        })}
        </div>
      </div>
    </div>
  );
};

export const DistributionFrame: React.FC = () => {
  const { filteredLines, hasSearched, selectedCore, setSelectedCore } = useAppStore();
  const [selectedBlockDetail, setSelectedBlockDetail] = useState<{ block: Block; lines: any[]; side: Side; context: 'inner' | 'outer' } | null>(null);
  
  // State for collapsible sections
  // Initial state: true (collapsed)
  const [innerCollapsed, setInnerCollapsed] = useState(true);
  const [outerCollapsed, setOuterCollapsed] = useState(true);

  // Group lines by line_type (backward compatibility: North=Inner, South=Outer if line_type is missing)
  const innerLines = useMemo(() => filteredLines.filter(l => {
      const type = l.line_type?.trim();
      const side = l.side?.trim();
      return type === '' || (!type && side === '');
  }), [filteredLines]);

  const outerLines = useMemo(() => filteredLines.filter(l => {
      const type = l.line_type?.trim();
      const side = l.side?.trim();
      return type === '' || (!type && side === '');
  }), [filteredLines]);

  // Calculate active blocks for highlighting in BlockView
  // We use "Side-Block" format (e.g. "-A") to match BlockView's expectation when ignoreSideFilter is false
  const innerActiveBlocks = useMemo(() => {
    const blocks = new Set<string>();
    innerLines.forEach(l => {
        const side = l.side?.trim();
        const block = l.block?.trim();
        if (side && block) blocks.add(`${side}-${block}`);
    });
    return blocks;
  }, [innerLines]);

  const outerActiveBlocks = useMemo(() => {
    const blocks = new Set<string>();
    outerLines.forEach(l => {
        const side = l.side?.trim();
        const block = l.block?.trim();
        if (side && block) blocks.add(`${side}-${block}`);
    });
    return blocks;
  }, [outerLines]);

  const handleCellSelect = (lineType: string, core: string) => {
    if (!lineType) {
        setSelectedCore(null, null, null);
    } else {
        setSelectedCore(null, null, core, lineType);
    }
  };

  const handleBlockClick = (block: Block, lines: any[], side: Side, context: 'inner' | 'outer') => {
      if (
        selectedBlockDetail &&
        selectedBlockDetail.block === block &&
        selectedBlockDetail.side === side &&
        selectedBlockDetail.context === context
      ) {
        setSelectedBlockDetail(null);
      } else {
        setSelectedBlockDetail({ block, lines, side, context });
      }
  };

  if (innerLines.length === 0 && outerLines.length === 0) {
    return (
      <div className="text-center py-12 text-slate-500">
        
      </div>
    );
  }

  return (
    <div className="space-y-4 animate-in fade-in duration-500 p-0 md:p-2 relative">
      
      {/* Inner Lines Section */}
      <section className="space-y-4 bg-transparent p-0">
          <div className="flex flex-col md:flex-row gap-4 items-center md:items-start">
             <div className="flex-shrink-0 text-center md:text-left space-y-4">
               <div 
                 className="text-base font-bold mb-3 flex items-center gap-2 text-slate-100 justify-center md:justify-start cursor-pointer select-none hover:text-blue-400 transition-colors"
                 onClick={() => setInnerCollapsed(!innerCollapsed)}
               >
                 <span className="w-3 h-3 bg-blue-500 rounded-full shadow-[0_0_10px_rgba(59,130,246,0.8)]"></span>
                 
                 {innerCollapsed ? <ChevronDown className="w-4 h-4" /> : <ChevronUp className="w-4 h-4" />}
               </div>
               
               {!innerCollapsed && (
                 <div className="flex flex-row flex-wrap justify-center gap-2 md:flex-col md:justify-start md:gap-4 animate-in slide-in-from-top-2 duration-200">
                   {/* North Layout for Inner */}
                   <div className="order-1 md:order-none bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                     <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                     <BlockView 
                       side="" 
                       layout={NORTH_LAYOUT} 
                       activeBlocks={innerActiveBlocks} 
                       filteredLines={innerLines}
                       hasSearched={hasSearched}
                       onBlockClick={(b, l, s) => handleBlockClick(b, l, s, 'inner')}
                       ignoreSideFilter={false}
                     />
                   </div>
                   {selectedBlockDetail?.context === 'inner' && selectedBlockDetail.side === '' && (
                     <div className="order-3 md:order-none w-full basis-full">
                       <BlockDetailList 
                          detail={selectedBlockDetail} 
                          onClose={() => setSelectedBlockDetail(null)} 
                       />
                     </div>
                   )}
    
                   {/* South Layout for Inner */}
                   <div className="order-2 md:order-none bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                     <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                     <BlockView 
                       side="" 
                       layout={SOUTH_LAYOUT} 
                       activeBlocks={innerActiveBlocks} 
                       filteredLines={innerLines}
                       hasSearched={hasSearched}
                       onBlockClick={(b, l, s) => handleBlockClick(b, l, s, 'inner')}
                       ignoreSideFilter={false}
                     />
                   </div>
                   {selectedBlockDetail?.context === 'inner' && selectedBlockDetail.side === '' && (
                     <div className="order-3 md:order-none w-full basis-full">
                       <BlockDetailList 
                          detail={selectedBlockDetail} 
                          onClose={() => setSelectedBlockDetail(null)} 
                       />
                     </div>
                   )}
                 </div>
               )}
             </div>
             
             {/* Inner Grid (Single) */}
             <div className="flex-1 w-full">
               {innerLines.length > 0 ? (
                  <CoreGrid
                    title=""
                    colorClass="bg-blue-500 text-blue-500"
                    data={innerLines}
                    selectedCore={selectedCore}
                    lineType=""
                    onSelect={handleCellSelect}
                  />
               ) : (
                 <div className="text-slate-500 text-sm py-8 text-center md:text-left border-2 border-dashed border-slate-800 rounded-lg">
                   
                 </div>
               )}
             </div>
          </div>
      </section>

      {/* Outer Lines Section */}
      <section className="space-y-4 bg-transparent p-0">
          <div className="flex flex-col md:flex-row gap-4 items-center md:items-start">
             <div className="flex-shrink-0 text-center md:text-left space-y-4">
               <div 
                 className="text-base font-bold mb-3 flex items-center gap-2 text-slate-100 justify-center md:justify-start cursor-pointer select-none hover:text-green-400 transition-colors"
                 onClick={() => setOuterCollapsed(!outerCollapsed)}
               >
                 <span className="w-3 h-3 bg-green-500 rounded-full shadow-[0_0_10px_rgba(34,197,94,0.8)]"></span>
                 
                 {outerCollapsed ? <ChevronDown className="w-4 h-4" /> : <ChevronUp className="w-4 h-4" />}
               </div>
               
               {!outerCollapsed && (
                 <div className="flex flex-row flex-wrap justify-center gap-2 md:flex-col md:justify-start md:gap-4 animate-in slide-in-from-top-2 duration-200">
                   {/* North Layout for Outer */}
                   <div className="order-1 md:order-none bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                     <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                     <BlockView 
                       side="" 
                       layout={NORTH_LAYOUT} 
                       activeBlocks={outerActiveBlocks} 
                       filteredLines={outerLines}
                       hasSearched={hasSearched}
                       onBlockClick={(b, l, s) => handleBlockClick(b, l, s, 'outer')}
                       ignoreSideFilter={false}
                     />
                   </div>
                   {selectedBlockDetail?.context === 'outer' && selectedBlockDetail.side === '' && (
                     <div className="order-3 md:order-none w-full basis-full">
                       <BlockDetailList 
                          detail={selectedBlockDetail} 
                          onClose={() => setSelectedBlockDetail(null)} 
                       />
                     </div>
                   )}
    
                   {/* South Layout for Outer */}
                   <div className="order-2 md:order-none bg-slate-800/30 p-2 rounded-lg border border-slate-700/50">
                     <div className="text-xs text-slate-400 mb-1 text-center font-bold"></div>
                     <BlockView 
                       side="" 
                       layout={SOUTH_LAYOUT} 
                       activeBlocks={outerActiveBlocks} 
                       filteredLines={outerLines}
                       hasSearched={hasSearched}
                       onBlockClick={(b, l, s) => handleBlockClick(b, l, s, 'outer')}
                       ignoreSideFilter={false}
                     />
                   </div>
                   {selectedBlockDetail?.context === 'outer' && selectedBlockDetail.side === '' && (
                     <div className="order-3 md:order-none w-full basis-full">
                       <BlockDetailList 
                          detail={selectedBlockDetail} 
                          onClose={() => setSelectedBlockDetail(null)} 
                       />
                     </div>
                   )}
                 </div>
               )}
             </div>

             {/* Outer Grid (Single) */}
             <div className="flex-1 w-full">
               {outerLines.length > 0 ? (
                  <CoreGrid
                    title=""
                    colorClass="bg-green-500 text-green-500"
                    data={outerLines}
                    selectedCore={selectedCore}
                    lineType=""
                    onSelect={handleCellSelect}
                  />
               ) : (
                 <div className="text-slate-500 text-sm py-8 text-center md:text-left border-2 border-dashed border-slate-800 rounded-lg">
                    
                 </div>
               )}
             </div>
          </div>
      </section>

    </div>
  );
};