import React, { useEffect } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { DistributionFrame } from '@/components/DistributionFrame';
import { DataEntryForm } from '@/components/DataEntryForm';
import { SearchPanel } from '@/components/SearchPanel';
import { ExportButton } from '@/components/ExportButton';
import { ImportButton } from '@/components/ImportButton';
import { DetailModal } from '@/components/DetailModal';
import { LoginModal } from '@/components/LoginModal';
import { Phone, Search, Database, ListPlus, LogOut } from 'lucide-react';
import { cn } from '@/lib/utils';

function App() {
  const { fetchLines, hasSearched, filteredLines, activeTab, setActiveTab, isAuthenticated, logout } = useAppStore();
  const [isLoginOpen, setIsLoginOpen] = React.useState(false);
  const [pendingAction, setPendingAction] = React.useState<(() => void) | null>(null);

  useEffect(() => {
    fetchLines();
  }, [fetchLines]);

  const handleTabChange = (tab: 'search' | 'manage') => {
    if (tab === 'manage' && !isAuthenticated) {
      setPendingAction(() => () => setActiveTab('manage'));
      setIsLoginOpen(true);
    } else {
      setActiveTab(tab);
    }
  };

  const handleLoginSuccess = () => {
    if (pendingAction) {
      pendingAction();
      setPendingAction(null);
    }
  };

  return (
    <div className="min-h-screen bg-slate-950 flex flex-col font-sans text-slate-100">
      {/* Header */}
      <header className="bg-slate-900 border-b border-slate-800 sticky top-0 z-40">
        <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8 h-14 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <div className="bg-blue-600 p-1 rounded-md shadow-[0_0_10px_rgba(37,99,235,0.5)]">
              <Phone className="w-5 h-5 text-white" />
            </div>
            <h1 className="text-base font-bold tracking-tight text-white"></h1>
          </div>
          
          {/* Tab Navigation */}
          <div className="flex bg-slate-800 p-0.5 rounded-lg">
            <button
              onClick={() => handleTabChange('search')}
              className={cn(
                "flex items-center gap-1 px-3 py-1.5 rounded-md text-xs font-medium transition-all active:scale-95",
                activeTab === 'search' 
                  ? "bg-slate-700 text-blue-400 shadow-sm ring-1 ring-slate-600" 
                  : "text-slate-400 hover:text-slate-200 hover:bg-slate-700/50"
              )}
            >
              <Search className="w-3.5 h-3.5" />
              
            </button>
            <button
              onClick={() => handleTabChange('manage')}
              className={cn(
                "flex items-center gap-1 px-3 py-1.5 rounded-md text-xs font-medium transition-all active:scale-95",
                activeTab === 'manage' 
                  ? "bg-slate-700 text-blue-400 shadow-sm ring-1 ring-slate-600" 
                  : "text-slate-400 hover:text-slate-200 hover:bg-slate-700/50"
              )}
            >
              <ListPlus className="w-3.5 h-3.5" />
              
            </button>
          </div>
          
          {isAuthenticated && (
            <button
              onClick={logout}
              className="ml-2 p-1.5 text-slate-400 hover:text-red-400 hover:bg-slate-800 rounded-full transition-colors"
              title=""
            >
              <LogOut className="w-4 h-4" />
            </button>
          )}
        </div>
      </header>

      {/* Main Content */}
      <main className="flex-1 max-w-7xl mx-auto w-full px-2 sm:px-6 lg:px-8 py-4 space-y-4">
        
        {/* Visualization Section - Conditionally Rendered */}
        {hasSearched && filteredLines.length > 0 && activeTab === 'search' && (
          <section className="bg-slate-900 rounded-lg shadow-lg border border-slate-800 p-3 animate-in fade-in slide-in-from-bottom-4 duration-500">
            <div className="flex items-center justify-between mb-3">
              <h2 className="text-sm font-semibold flex items-center gap-2 text-slate-100">
                <span className="w-1 h-4 bg-blue-500 rounded-full shadow-[0_0_8px_rgba(59,130,246,0.8)]"></span>
                
              </h2>
              <div className="flex items-center gap-2 text-xs text-slate-400">
                <span className="flex items-center gap-1">
                  <span className="w-1.5 h-1.5 rounded-full bg-red-500 animate-pulse shadow-[0_0_5px_rgba(239,68,68,0.8)]"></span>
                  
                </span>
              </div>
            </div>
            <DistributionFrame />
          </section>
        )}

        {/* Search Tab Content */}
        {activeTab === 'search' && (
          <div className="max-w-3xl mx-auto space-y-6">
            <SearchPanel />
            
            {hasSearched && filteredLines.length === 0 && (
               <div className="text-center py-12 bg-slate-900 rounded-lg border border-slate-800 border-dashed">
                 <Database className="w-12 h-12 text-slate-700 mx-auto mb-3" />
                 <h3 className="text-lg font-medium text-slate-200"></h3>
                 <p className="text-slate-500"></p>
               </div>
            )}
          </div>
        )}

        {/* Manage Tab Content */}
        {activeTab === 'manage' && (
          <div className="max-w-2xl mx-auto space-y-4">
            {/* Import/Export Actions */}
            <div className="bg-slate-900/50 p-2 rounded-lg border border-slate-800 flex items-center justify-between gap-4">
              <span className="text-xs text-slate-500 font-medium ml-2"></span>
              <div className="flex gap-2">
                <ImportButton onAuthRequired={(action) => {
                    setPendingAction(() => action);
                    setIsLoginOpen(true);
                }} />
                <ExportButton />
              </div>
            </div>
            
            <DataEntryForm />
          </div>
        )}

      </main>

      {/* Footer */}
      <footer className="bg-slate-900 border-t border-slate-800 py-6 mt-auto">
        <div className="max-w-7xl mx-auto px-4 text-center text-sm text-slate-500">
           {new Date().getFullYear()}  | Ver 1.2.0
        </div>
      </footer>

      {/* Modals */}
      <DetailModal />
      <LoginModal 
        isOpen={isLoginOpen} 
        onClose={() => {
          setIsLoginOpen(false);
          setPendingAction(null);
        }}
        onSuccess={handleLoginSuccess}
      />
    </div>
  );
}

export default App;
