import { create } from 'zustand';
import { supabase } from '@/lib/supabase';
import { TelephoneLine, TelephoneLineFormData } from '@/types';

interface AppState {
  lines: TelephoneLine[];
  filteredLines: TelephoneLine[];
  loading: boolean;
  selectedBlock: { side: string; block: string } | null;
  selectedLinesInBlock: TelephoneLine[];
  selectedCore: { side?: string; block?: string; core: string; line_type?: string } | null;
  selectedLinesAtCore: TelephoneLine[];
  hasSearched: boolean;
  activeTab: 'search' | 'manage';
  editingLine: TelephoneLine | null;
  isAuthenticated: boolean;
  login: (password: string) => boolean;
  logout: () => void;
  
  fetchLines: () => Promise<void>;
  searchLines: (query: Partial<TelephoneLineFormData>) => void;
  resetSearch: () => void;
  addLine: (line: TelephoneLineFormData) => Promise<void>;
  updateLine: (id: string, line: TelephoneLineFormData) => Promise<void>;
  deleteLine: (id: string) => Promise<void>;
  importLines: (lines: TelephoneLineFormData[]) => Promise<{ added: number; updated: number }>;
  setSelectedBlock: (side: string | null, block: string | null) => void;
  setSelectedCore: (side: string | null, block: string | null, core: string | null, line_type?: string | null) => void;
  setActiveTab: (tab: 'search' | 'manage') => void;
  setEditingLine: (line: TelephoneLine | null) => void;
}

export const useAppStore = create<AppState>((set, get) => ({
  lines: [],
  filteredLines: [],
  loading: false,
  selectedBlock: null,
  selectedLinesInBlock: [],
  selectedCore: null,
  selectedLinesAtCore: [],
  hasSearched: false,
  activeTab: 'search',
  editingLine: null,
  isAuthenticated: false,

  login: (password: string) => {
    if (password === '122232') {
      set({ isAuthenticated: true });
      return true;
    }
    return false;
  },

  logout: () => {
    set({ isAuthenticated: false, activeTab: 'search' });
  },

  fetchLines: async () => {
    set({ loading: true });
    const { data, error } = await supabase
      .from('telephone_lines')
      .select('*')
      .order('created_at', { ascending: false });

    if (error) {
      console.error('Error fetching lines:', error);
      set({ loading: false });
      return;
    }

    set({ 
      lines: data as TelephoneLine[], 
      // filteredLines  hasSearched 
      filteredLines: [], 
      loading: false 
    });
  },

  searchLines: (query) => {
    const { lines } = get();
    // 
    const isQueryEmpty = Object.values(query).every(x => x === '' || x === undefined);
    
    if (isQueryEmpty) {
        set({ filteredLines: [], hasSearched: false, selectedBlock: null, selectedLinesInBlock: [] });
        return;
    }

    const { company_name, phone_number, core_number, exchange_info, side, block, block_core } = query;

    const filtered = lines.filter((line) => {
      const matchCompany = !company_name || line.company_name.includes(company_name);
      const matchPhone = !phone_number || line.phone_number.includes(phone_number);
      const matchCore = !core_number || line.core_number.includes(core_number);
      const matchExchange = !exchange_info || (line.exchange_info && line.exchange_info.includes(exchange_info));
      const matchSide = !side || line.side?.trim() === side.trim();
      const matchBlock = !block || line.block?.trim() === block.trim();
      
      let matchBlockCore = true;
      if (block_core) {
        // 1.  block_core 
        const directMatch = line.block_core && line.block_core.toLowerCase().includes(block_core.toLowerCase());
        
        // 2.  block_core ( "A-1")  block  core_number
        //  "Block-Core"  "BlockCore"
        let parsedMatch = false;
        const parts = block_core.split(/[-]/);
        if (parts.length === 2) {
             const qBlock = parts[0].toUpperCase();
             const qCore = parts[1];
             if (line.block === qBlock && line.core_number === qCore) {
                 parsedMatch = true;
             }
        } else if (block_core.length >= 2) {
             //  "A1" 
             const qBlock = block_core.charAt(0).toUpperCase();
             const qCore = block_core.substring(1);
             //  (Block) (Core)
             if (/^[A-J]$/.test(qBlock) && /^\d+$/.test(qCore)) {
                 if (line.block === qBlock && line.core_number === qCore) {
                     parsedMatch = true;
                 }
             }
        }

        matchBlockCore = Boolean(directMatch || parsedMatch);
      }

      return matchCompany && matchPhone && matchCore && matchExchange && matchSide && matchBlock && matchBlockCore;
    });

    set({ filteredLines: filtered, hasSearched: true, selectedBlock: null, selectedLinesInBlock: [] });
  },

  resetSearch: () => {
    set({ filteredLines: [], hasSearched: false, selectedBlock: null, selectedLinesInBlock: [] });
  },

  addLine: async (formData) => {
    set({ loading: true });
    const { error } = await supabase
      .from('telephone_lines')
      .insert([formData]);

    if (error) {
      console.error('Error adding line:', error);
      throw error;
    }

    // Refresh lines
    await get().fetchLines();
    set({ loading: false });
  },

  updateLine: async (id, formData) => {
    set({ loading: true });
    const { error } = await supabase
      .from('telephone_lines')
      .update(formData)
      .eq('id', id);

    if (error) {
      console.error('Error updating line:', error);
      throw error;
    }

    await get().fetchLines();
    set({ loading: false, editingLine: null });
  },

  importLines: async (importedData) => {
    set({ loading: true });
    try {
      // 1. Get current lines to check for duplicates
      const { data: existingLines, error: fetchError } = await supabase
        .from('telephone_lines')
        .select('*');
      
      if (fetchError) throw fetchError;

      const existingMap = new Map();
      existingLines?.forEach(line => {
        // Key: Side-Block-Core (Unique combination)
        const key = `${line.side}-${line.block}-${line.core_number}`;
        existingMap.set(key, line.id);
      });

      const updates: any[] = [];
      const inserts: any[] = [];

      importedData.forEach(item => {
        const key = `${item.side}-${item.block}-${item.core_number}`;
        const existingId = existingMap.get(key);
        
        // Ensure line_type is set if missing (default based on side)
        const lineItem = {
            ...item,
            line_type: item.line_type || (item.side === '' ? '' : '')
        };

        if (existingId) {
          updates.push({ ...lineItem, id: existingId, updated_at: new Date().toISOString() });
        } else {
          inserts.push(lineItem);
        }
      });

      if (updates.length > 0) {
        const { error } = await supabase
          .from('telephone_lines')
          .upsert(updates);
        if (error) throw error;
      }

      if (inserts.length > 0) {
        const { error } = await supabase
          .from('telephone_lines')
          .insert(inserts);
        if (error) throw error;
      }

      await get().fetchLines();
      
      set({ loading: false });
      return { added: inserts.length, updated: updates.length };
    } catch (error) {
      console.error('Error importing lines:', error);
      set({ loading: false });
      throw error;
    }
  },

  deleteLine: async (id) => {
    set({ loading: true });
    const { error } = await supabase
      .from('telephone_lines')
      .delete()
      .eq('id', id);

    if (error) {
      console.error('Error deleting line:', error);
      alert(`: ${error.message}`);
      throw error;
    }

    await get().fetchLines();
    // Refresh selections
    const { selectedCore, lines } = get();
    if (selectedCore) {
         const { side, block, core, line_type } = selectedCore;
         let linesAtCore: TelephoneLine[] = [];
         
         if (line_type) {
             linesAtCore = lines.filter(l => {
                 const lType = l.line_type || (l.side === '' ? '' : '');
                 return l.core_number === core && lType === line_type;
             });
         } else if (side && block) {
             linesAtCore = lines.filter(l => l.side === side && l.block === block && l.core_number === core);
         }

         set({ selectedLinesAtCore: linesAtCore });
         if (linesAtCore.length === 0) {
            set({ selectedCore: null });
         }
    }
    set({ loading: false });
  },

  setSelectedBlock: (side, block) => {
    // Legacy support or removal? Keeping for safety but won't be used
    if (!side || !block) {
      set({ selectedBlock: null, selectedLinesInBlock: [] });
      return;
    }
    // ... logic ...
  },

  setSelectedCore: (side, block, core, line_type) => {
    if (!core) {
      set({ selectedCore: null, selectedLinesAtCore: [] });
      return;
    }

    const { filteredLines, hasSearched, lines } = get();
    const sourceLines = hasSearched ? filteredLines : lines;
    
    const linesAtCore = sourceLines.filter((line) => {
        const matchCore = line.core_number === core;
        
        if (line_type) {
            // Filter by line_type
            const currentLineType = line.line_type || (line.side === '' ? '' : '');
            return matchCore && currentLineType === line_type;
        }
        
        // Legacy/Fallback: Filter by side and block
        if (side && block) {
            return matchCore && line.side === side && line.block === block;
        }
        
        return matchCore;
    });

    set({ 
        selectedCore: { 
            side: side || undefined, 
            block: block || undefined, 
            core,
            line_type: line_type || undefined 
        }, 
        selectedLinesAtCore: linesAtCore,
    });
  },
  
  setActiveTab: (tab) => set({ activeTab: tab }),
  setEditingLine: (line) => set({ editingLine: line }),
}));
