import React, { useState } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { Search, X } from 'lucide-react';
import { Side } from '@/types';

export const SearchPanel: React.FC = () => {
  const { searchLines, resetSearch } = useAppStore();
  const [searchParams, setSearchParams] = useState({
    company_name: '',
    phone_number: '',
    core_number: '',
    exchange_info: '',
    side: '' as Side | '',
    block_core: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setSearchParams((prev) => ({ ...prev, [name]: value }));
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    const query = {
      ...searchParams,
      side: searchParams.side === '' ? undefined : searchParams.side,
    };
    searchLines(query);
  };

  const handleReset = () => {
    setSearchParams({
      company_name: '',
      phone_number: '',
      core_number: '',
      exchange_info: '',
      side: '',
      block_core: '',
    });
    resetSearch();
  };

  const inputClass = "w-full border border-slate-700 bg-slate-800 text-slate-200 rounded-md p-1.5 text-sm outline-none focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-colors placeholder:text-slate-500";

  return (
    <div className="bg-slate-900 p-4 rounded-lg shadow-lg border border-slate-800">
      <h3 className="text-base font-bold mb-3 border-b border-slate-800 pb-2 flex items-center text-slate-200">
        <Search className="w-4 h-4 mr-2 text-blue-500" />
        
      </h3>
      <form onSubmit={handleSearch} className="space-y-3">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
          <input
            type="text"
            name="company_name"
            value={searchParams.company_name}
            onChange={handleChange}
            placeholder=""
            className={inputClass}
          />
          <input
            type="text"
            name="phone_number"
            value={searchParams.phone_number}
            onChange={handleChange}
            placeholder=""
            className={inputClass}
          />
          <input
            type="text"
            name="core_number"
            value={searchParams.core_number}
            onChange={handleChange}
            placeholder=""
            className={inputClass}
          />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
          <input
            type="text"
            name="exchange_info"
            value={searchParams.exchange_info}
            onChange={handleChange}
            placeholder=""
            className={inputClass}
          />
          <select
            name="side"
            value={searchParams.side}
            onChange={handleChange}
            className={inputClass}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <input
            type="text"
            name="block_core"
            value={searchParams.block_core}
            onChange={handleChange}
            placeholder=" (ex: A-1)"
            className={inputClass}
          />
        </div>
        
        <div className="flex gap-2 pt-1">
          <button
            type="submit"
            className="flex-1 bg-blue-600 text-white py-1.5 rounded-md hover:bg-blue-700 transition-all flex items-center justify-center active:scale-95 shadow-md shadow-blue-900/20 text-sm"
          >
            <Search className="w-3.5 h-3.5 mr-2" />
            
          </button>
          <button
            type="button"
            onClick={handleReset}
            className="px-4 py-1.5 border border-slate-700 rounded-md hover:bg-slate-800 transition-all flex items-center justify-center text-slate-300 active:scale-95 text-sm"
          >
            <X className="w-3.5 h-3.5 mr-2" />
            
          </button>
        </div>
      </form>
    </div>
  );
};
