import React, { useRef } from 'react';
import * as XLSX from 'xlsx';
import { useAppStore } from '@/store/useAppStore';
import { Upload, Loader2 } from 'lucide-react';
import { TelephoneLineFormData, Side, Block } from '@/types';

interface ImportButtonProps {
  onAuthRequired: (action: () => void) => void;
}

export const ImportButton: React.FC<ImportButtonProps> = ({ onAuthRequired }) => {
  const { importLines, loading, isAuthenticated } = useAppStore();
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleButtonClick = () => {
    if (isAuthenticated) {
      fileInputRef.current?.click();
    } else {
      onAuthRequired(() => fileInputRef.current?.click());
    }
  };

  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // Reset value so same file can be selected again
    e.target.value = '';

    const reader = new FileReader();
    reader.onload = async (evt) => {
      try {
        const bstr = evt.target?.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        const data = XLSX.utils.sheet_to_json(ws);

        if (data.length === 0) {
          alert('');
          return;
        }

        // Validate and map data
        const formattedData: TelephoneLineFormData[] = [];
        const errors: string[] = [];

        data.forEach((row: any, index) => {
          const rowNum = index + 2; // Header is row 1
          
          const side = row[''];
          const block = row[''];
          const core_number = row[''];
          const line_type_raw = row[''];
          
          if (!side || !block) {
            errors.push(` ${rowNum}  ()`);
            return;
          }

          if (side !== '' && side !== '') {
             errors.push(` ${rowNum}  (   )`);
             return;
          }
          
          // Block validation
          const validBlocks = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', '', '', ''];
          if (!validBlocks.includes(block)) {
             errors.push(` ${rowNum}  ( A-J  )`);
             return;
          }

          if (core_number) {
            const coreNum = parseInt(core_number);
            if (isNaN(coreNum) || coreNum < 1 || coreNum > 100) {
                errors.push(` ${rowNum}  ( 1-100)`);
                return;
            }
          }

          formattedData.push({
            side: side as Side,
            block: block as Block,
            line_type: (line_type_raw as '' | '') || (side === '' ? '' : ''),
            core_number: core_number ? core_number.toString() : '',
            block_core: row[''] || row[''] || '', // Handle both old and new names potentially
            company_name: row[''] || '',
            phone_number: row[''] || '',
            exchange_info: row[''] || '',
            remarks: row[''] || ''
          });
        });

        if (errors.length > 0) {
            alert(`\n${errors.slice(0, 10).join('\n')}${errors.length > 10 ? '\n...' : ''}`);
            return;
        }

        if (window.confirm(` ${formattedData.length} \n (++) \n`)) {
            const result = await importLines(formattedData);
            alert(`\n: ${result.added} \n: ${result.updated} `);
        }

      } catch (error: any) {
        console.error('Import error:', error);
        alert(`: ${error.message}`);
      }
    };
    reader.readAsBinaryString(file);
  };

  return (
    <>
      <input
        type="file"
        ref={fileInputRef}
        onChange={handleFileChange}
        className="hidden"
        accept=".xlsx, .xls"
      />
      <button
        onClick={handleButtonClick}
        disabled={loading}
        className="flex items-center px-3 py-1.5 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-all active:scale-95 shadow-md shadow-blue-900/20 text-xs disabled:opacity-50 disabled:cursor-not-allowed"
      >
        {loading ? <Loader2 className="w-3.5 h-3.5 mr-1.5 animate-spin" /> : <Upload className="w-3.5 h-3.5 mr-1.5" />}
        
      </button>
    </>
  );
};
