import React, { useState } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { X, Pencil, Trash2 } from 'lucide-react';
import { TelephoneLine } from '@/types';
import { LoginModal } from './LoginModal';

export const DetailModal: React.FC = () => {
  const { 
    selectedCore,
    selectedLinesAtCore,
    setSelectedCore, 
    deleteLine, 
    setEditingLine, 
    setActiveTab,
    isAuthenticated
  } = useAppStore();

  const [isLoginOpen, setIsLoginOpen] = useState(false);
  const [pendingAction, setPendingAction] = useState<(() => void) | null>(null);

  if (!selectedCore) return null;

  const handleAction = (action: () => void) => {
    if (isAuthenticated) {
      action();
    } else {
      setPendingAction(() => action);
      setIsLoginOpen(true);
    }
  };

  const handleEdit = (line: TelephoneLine) => {
    handleAction(() => {
        setEditingLine(line);
        setActiveTab('manage');
        setSelectedCore(null, null, null, null);
    });
  };

  const handleDelete = async (id: string) => {
    handleAction(async () => {
        if (window.confirm('')) {
            try {
              await deleteLine(id);
            } catch (error) {
              // Error already alerted in store
            }
        }
    });
  };

  const handleLoginSuccess = () => {
    if (pendingAction) {
      pendingAction();
      setPendingAction(null);
    }
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/70 p-4 backdrop-blur-sm">
      <div className="bg-slate-900 rounded-lg shadow-2xl w-full max-w-2xl max-h-[80vh] overflow-hidden flex flex-col animate-in fade-in zoom-in duration-200 border border-slate-800">
        <LoginModal 
            isOpen={isLoginOpen} 
            onClose={() => {
                setIsLoginOpen(false);
                setPendingAction(null);
            }}
            onSuccess={handleLoginSuccess}
        />
        <div className="flex items-center justify-between p-4 border-b border-slate-800">
          <h3 className="text-xl font-bold text-slate-100">
            {selectedCore.line_type ? (
                <>{selectedCore.line_type} -  {selectedCore.core} </>
            ) : (
                <>{selectedCore.side} -  {selectedCore.block} -  {selectedCore.core} </>
            )}
          </h3>
          <button
            onClick={() => setSelectedCore(null, null, null)}
            className="p-1 hover:bg-slate-800 rounded-full transition-colors text-slate-400 hover:text-slate-200 active:scale-95"
          >
            <X className="w-6 h-6" />
          </button>
        </div>
        
        <div className="p-4 overflow-y-auto">
          {selectedLinesAtCore.length === 0 ? (
            <p className="text-center text-slate-500 py-8"></p>
          ) : (
            <div className="space-y-4">
              {selectedLinesAtCore.map((line) => (
                <div key={line.id} className="border border-slate-700 rounded-lg p-4 hover:bg-slate-800/50 transition-colors group bg-slate-800/30">
                  <div className="flex justify-between items-start">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-x-8 gap-y-2 flex-1 text-slate-300">
                      <p><span className="font-semibold text-slate-400"></span>{line.company_name}</p>
                      <p><span className="font-semibold text-slate-400"></span>{line.phone_number}</p>
                      <p><span className="font-semibold text-slate-400"></span>{line.core_number}</p>
                      <p><span className="font-semibold text-slate-400"></span>{line.block_core || '-'}</p>
                      <p><span className="font-semibold text-slate-400"></span>{line.line_type || (line.side === '' ? '' : '')}</p>
                      <p><span className="font-semibold text-slate-400"></span>{line.exchange_info || '-'}</p>
                    </div>
                    <div className="flex gap-2 ml-4 opacity-100 md:opacity-0 group-hover:opacity-100 transition-opacity">
                      <button
                        onClick={() => handleEdit(line)}
                        className="p-2 text-blue-400 hover:bg-slate-700 rounded-full transition-colors active:scale-95"
                        title=""
                      >
                        <Pencil className="w-4 h-4" />
                      </button>
                      <button
                        onClick={() => handleDelete(line.id)}
                        className="p-2 text-red-400 hover:bg-slate-700 rounded-full transition-colors active:scale-95"
                        title=""
                      >
                        <Trash2 className="w-4 h-4" />
                      </button>
                    </div>
                  </div>
                  {line.remarks && (
                    <div className="mt-2 pt-2 border-t border-slate-700">
                      <p className="text-slate-300"><span className="font-semibold text-slate-400"></span>{line.remarks}</p>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
        
        <div className="p-4 border-t border-slate-800 bg-slate-900 text-right">
          <button
            onClick={() => setSelectedCore(null, null, null, null)}
            className="px-4 py-2 bg-slate-700 text-white rounded-md hover:bg-slate-600 transition-all active:scale-95 shadow-md shadow-slate-900/20"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};
