import React, { useState } from 'react';
import { useAppStore } from '@/store/useAppStore';
import { Lock, X } from 'lucide-react';

interface LoginModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
  title?: string;
}

export const LoginModal: React.FC<LoginModalProps> = ({ isOpen, onClose, onSuccess, title = "" }) => {
  const { login } = useAppStore();
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  if (!isOpen) return null;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (login(password)) {
      setError('');
      setPassword('');
      onSuccess();
      onClose();
    } else {
      setError('');
      setPassword('');
    }
  };

  return (
    <div className="fixed inset-0 z-[60] flex items-center justify-center bg-black/70 p-4 backdrop-blur-sm animate-in fade-in duration-200">
      <div className="bg-slate-900 rounded-lg shadow-2xl w-full max-w-sm border border-slate-800 p-6 relative">
        <button
          onClick={onClose}
          className="absolute top-4 right-4 text-slate-400 hover:text-slate-200"
        >
          <X className="w-5 h-5" />
        </button>
        
        <div className="flex flex-col items-center mb-6">
          <div className="bg-slate-800 p-3 rounded-full mb-3 shadow-[0_0_15px_rgba(30,41,59,0.5)]">
            <Lock className="w-8 h-8 text-blue-500" />
          </div>
          <h3 className="text-xl font-bold text-slate-100">{title}</h3>
          <p className="text-slate-400 text-sm mt-1"></p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=""
              className="w-full bg-slate-950 border border-slate-700 rounded-md px-4 py-2 text-slate-100 placeholder:text-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-center tracking-widest"
              autoFocus
            />
            {error && <p className="text-red-500 text-sm mt-2 text-center">{error}</p>}
          </div>
          
          <button
            type="submit"
            className="w-full bg-blue-600 hover:bg-blue-500 text-white font-medium py-2 rounded-md transition-colors shadow-lg shadow-blue-900/20 active:scale-95"
          >
            
          </button>
        </form>
      </div>
    </div>
  );
};
