import React from 'react';
import * as XLSX from 'xlsx';
import { useAppStore } from '@/store/useAppStore';
import { Download } from 'lucide-react';

export const ExportButton: React.FC = () => {
  const { filteredLines, lines } = useAppStore();

  const handleExport = (exportAll: boolean) => {
    const dataToExport = exportAll ? lines : filteredLines;
    
    if (dataToExport.length === 0) {
      alert('');
      return;
    }

    // Format data for export
    const exportData = dataToExport.map(line => ({
      '': line.side,
      '': line.block,
      '': line.block_core || '',
      '': line.line_type || (line.side === '' ? '' : ''),
      '': line.company_name,
      '': line.phone_number,
      '': line.core_number,
      '': line.exchange_info || '',
      '': line.remarks || ''
    }));

    const ws = XLSX.utils.json_to_sheet(exportData);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "");

    const dateStr = new Date().toISOString().split('T')[0];
    XLSX.writeFile(wb, `_${exportAll ? '' : ''}_${dateStr}.xlsx`);
  };

  return (
    <div className="flex gap-2">
      <button
        onClick={() => handleExport(false)}
        className="flex items-center px-3 py-1.5 bg-green-600 text-white rounded-md hover:bg-green-700 transition-all active:scale-95 shadow-md shadow-green-900/20 text-xs"
      >
        <Download className="w-3.5 h-3.5 mr-1.5" />
        
      </button>
      <button
        onClick={() => handleExport(true)}
        className="flex items-center px-3 py-1.5 bg-slate-700 text-white rounded-md hover:bg-slate-600 transition-all active:scale-95 shadow-md shadow-slate-900/20 text-xs"
      >
        <Download className="w-3.5 h-3.5 mr-1.5" />
        
      </button>
    </div>
  );
};
